<template>
  <div>
    <el-header>
      <span class="headh">Web</span>
      <div class="toolbar">
        <div align="center" style="width:175px;height:60px;line-height:60px">
          <span> {{name}} </span>
        </div>
        
        <el-button class="headbutton" @click="Loginout()"></el-button>
      </div>
    </el-header>

    <el-container>
      <el-aside width="200px">
        <el-menu :default-openeds="['1','2']">
          <el-sub-menu index="1" >
            <template #title>
              <el-icon><message /></el-icon>
            </template>

              <el-menu-item index="1-1" >
                <router-link :to="{name:'Accountinfo',params:{phonenumber: phonenumber}}">
                  
                </router-link>
              </el-menu-item>
              <el-menu-item index="1-2" v-if="isAdmin">
                <router-link :to="{name:'Submanagement',params:{phonenumber: phonenumber }}">
                  
                </router-link>
              </el-menu-item>

          </el-sub-menu>

          <el-sub-menu index="2" v-if="isAdmin">
            <template #title>
              <el-icon><setting /></el-icon>
            </template>

              <el-menu-item index="2-1">
                <router-link :to="{name:'Commoditydata',params:{phonenumber: phonenumber}}">
                  
                </router-link>
              </el-menu-item>

              <el-menu-item index="2-2">
                <router-link :to="{name:'Inventoryorder',params:{phonenumber: phonenumber}}">
                  
                </router-link>
              </el-menu-item>

          </el-sub-menu>

          <!-- <el-menu-item>
            <router-link></router-link>
          </el-menu-item> -->

        </el-menu>
      </el-aside>

      <el-main>
          <router-view></router-view>
      </el-main>
    </el-container>

    <el-dialog title="" v-model="tipdialogVisible" width="30%">
        <span>{{adminphone}}<br></span>
          <el-button @click="tipdialogVisible=false;resetdialogVisible=true"></el-button>
        <span></span>
        <br><br><br>
        <span class="dialog-footer">
          <el-button type="primary" @click="tipdialogVisible=false"> </el-button>
        </span>
    </el-dialog>

    <el-dialog title="" v-model="resetdialogVisible" width="30%">
        <span><br></span>
        <br><br><br>
        <span class="dialog-footer">
          <el-button @click="onReset()"> </el-button>
          <el-button type="primary" @click="resetdialogVisible=false"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
  import { Menu as IconMenu, Message, Setting } from '@element-plus/icons-vue';
  import axios from '../http';
  import { ElMessage } from 'element-plus';

  export default {
    name: 'Main',
    props: ['phonenumber'],
    components: { IconMenu,Message,Setting },
    data() {
      return {
        name: '',
        isAdmin: false,
        adminphone: '',
        tipdialogVisible: false,
        resetdialogVisible: false,
      }
    },
    computed: {// data
    },
    // mounted() {
    created() {
      this.isAdminUser();
    },
    methods: {
      /********************    ************************/
      isAdminUser(){
        let that = this;
        axios.get(this.$store.state.url+"/webapi/userdb/get_is_admin_user",{
          params:{
            phonenumber: that.phonenumber
          }
        })
        .then(function(response){
          if(response.data.is==2){
            console.log('');
            that.isAdmin=true;
            that.name=response.data.name;
          }else if(response.data.is==1){
            console.log('');
            that.isAdmin=false;
            that.adminphone=response.data.adminphone;
            that.tipdialogVisible=true;
          }else if(response.data.is==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /******************************    ******************************/
      onReset() {
        let that = this;
        axios.post(this.$store.state.url+'/webapi/userdb/reset_userdata',{phonenumber: that.phonenumber}).then(function(response){
          if(response.data.reset==1){
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            sessionStorage.removeItem("loginstate");
            that.$router.push("/");
          }else if(response.data.reset==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');})
      },
      /***************************    ******************************/
      Loginout(){
        // cookie
        let that = this;
        axios.post(this.$store.state.url+'/webapi/Log_out',{
          phonenumber: that.phonenumber
        }).then(function(response){
          if(response.data.Logout==1){
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            console.log('');
          }else if(response.data.Logout==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');})
        localStorage.removeItem("loginstate");
        this.$router.push("/");
      },
      /****************************    *******************************/
      loginfail(){
        console.log('');
        sessionStorage.removeItem("loginstate");
        this.$router.push("/");
        alert('');
      },
      /***********************************************************************/
    }
  }

</script>

<style scoped>
.el-header {
  height: 61px;
  padding: 0px 20px 0px 20px;
  border-bottom: 1px solid var(--el-color-primary-light-3);
  text-align: right;
  font-size: 18px;
}
.headh {
  width: 275px;
  height: 26px;
  margin: 17px 0px 17px 0px;
  float: left;
}
.toolbar {
  display: inline-flex;
  width: 260px;
  height: 60px;
  align-items: center;
}
.headbutton {
  width: 75px;
  height: 55px;
  padding: 0px;
  margin: 0px;
}
.el-container {
  float: left;
  min-height: 1000px;
}
.el-aside {
  float: left;
  width: 221px;
  overflow: visible;
}
.el-main {
  width: 862px;
  padding: 30px;
  border-left: 1px solid var(--el-color-primary-light-3);
}

</style>
