<template>
  <div>
  <el-form class="updatePassword-box" label-width="80px">
    <h3 class="updatePassword-title"></h3>
    <el-steps :active="activeindex" :space="200" finish-status="success" align-center>
      <el-step title="1" description=""/>
      <el-step title="2" description=""/>
      <el-step title="3" description=""/>
    </el-steps>

    <el-tabs class="tabs-box" v-model="activeindex" >
      <div v-show="activeindex==1" v-on:keyup.enter="nextStep()">
        <el-form-item label="">
              <el-input type="text" placeholder="" v-model="form.phonenumber"/>
        </el-form-item>
      </div>
      <div v-show="activeindex==2" v-on:keyup.enter="nextStep()">
        <el-form-item label="">
              <el-input type="text" disabled="disabled" v-model="form.phonenumber"/>
        </el-form-item>
        <el-form-item label="">
              <el-input type="text" disabled="disabled" v-model="form.email"/>
        </el-form-item>
        <el-form-item label="">
              <el-input type="password" placeholder="" v-model="form.newpassword1"/>
        </el-form-item>
        <el-form-item label="">
              <el-input type="password" placeholder="" v-model="form.newpassword2"/>
        </el-form-item>
        <el-form-item class="verifyform" label="">
              <el-input class="verifytext" type="text" placeholder="" v-model="form.validcode"/>
              <el-button type="primary" :disabled="emailButtondisabled" v-on:click="sendEmailvalid()" >{{emailButtontext}}</el-button>
        </el-form-item>
        <el-form-item class="verifyform" label="">
              <el-input class="verifytext" type="text" placeholder="" v-model="form.captcha"/>
              <a class="verifyimg" @click="updateImgurl()">
                <img class="verifyimg" v-bind:src="form.imgurl" />
              </a>
        </el-form-item>
      </div>
      <div v-show="activeindex==3">
        <el-form ref="loginForm" label-width="80px" class="login-box">
          <h3 class="login-title">{{delayedtimeComplete}}</h3>
          <el-button type="primary" v-on:click="toLogin()"></el-button>
        </el-form>
      </div>
    </el-tabs>
    <el-button v-if="compbtn" type="primary" style="margin-bottom:50px;" v-on:click="nextStep()"></el-button>
    <el-button v-if="uppassbtn" style="margin-bottom:50px;" v-on:click="toLogin()"></el-button>
  </el-form>

  <el-dialog title="" v-model="dialogVisible" width="30%">
        <span>{{dialogtext}}</span><br>
        <span v-show="textVisible">820<br></span><br>
        <span class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from '../http';

  export default {
    name: "UpdatePassword",
    components: {  },
    data() {
      return {
        activeindex: 1,
        form: {
          phonenumber: '', email: '',
          newpassword1: '' ,newpassword2: '',
          validcode: '', captcha: '',
          imgurl: '', key: ''
        },
        emailButtontext: '',emailButtondisabled: false,
        timer: '',delayedtimeVerif: 60,delayedtimeComplete: 5,
        dialogtext: '',textVisible: false,dialogVisible: false,
        uppassbtn: true,compbtn: true
      }
    },
    computed: {},
    mounted: function(){
      // 
      // this.updateImgurl();
    },
    methods: {
      /************************    **************************/
      updateImgurl(){
        let that = this;
        axios.get(this.$store.state.url+"/captcha/api/math").then(function(response){
          console.log(response);
          that.form.imgurl = response.data.img;
          that.form.key = response.data.key
        },function(err){console.log('');});
      },
      /*****************  email  ******************/
      setUpdataPasswordUser(){
        let that = this;
        axios.post(this.$store.state.url+"/webapi/set_updata_password_user",{phonenumber: that.form.phonenumber}).then(function(response){
          if(response.data.setcomplete==1){
            that.form.email = response.data.email;
            that.uppassbtn=false;
            that.activeindex++;
            that.updateImgurl();
          }else if(response.data.setcomplete==0){
            that.dialogtext = '';
            that.textVisible = false;
            that.dialogVisible = true;
          }
        },function(err){console.log('');});
      },
      /************************    *************************/
      sendEmailvalid(){
        let that = this;
        axios.post(this.$store.state.url+"/webapi/send_emailvalidcode",{phonenumber: that.form.phonenumber}).then(function(response){
          that.startTimerVerif();
          that.emailButtondisabled = true;
        },function(err){console.log('');});
      },
      /********************   post  *******************/
      verifForm(){
        let that = this;
        axios.post(this.$store.state.url+"/webapi/verif_form",
        {
          phonenumber: this.form.phonenumber,
          newpassword1: this.form.newpassword1,
          newpassword2: this.form.newpassword2,
          validcode: this.form.validcode,
          captcha: this.form.captcha,
          key: this.form.key,
        }).then(function(response){
          console.log(response.data);
          if(response.data.verif==4){
            console.log('');
            that.stopTimerVerif();
            that.compbtn=false;
            that.activeindex++;
            that.startTimerComplete();
          }else if(response.data.verif==3){
            that.dialogtext = '';
            that.textVisible = true;
            that.dialogVisible = true;
          }else if(response.data.verif==2){
            that.dialogtext = '';
            that.textVisible = false;
            that.dialogVisible = true;
          }else if(response.data.verif==1){
            that.dialogtext = '';
            that.textVisible = false;
            that.dialogVisible = true;
            that.updateImgurl();
          }
        },function(err){console.log('');});
      },
      /**************************    **************************/
      nextStep(){
        if(this.activeindex==1){
          this.setUpdataPasswordUser();
        }else if(this.activeindex==2){
          this.verifForm();
        }
      },
      /************************    **************************/
      /********************     ***********************/
      // 
      startTimerVerif(){
        this.timer = setInterval(this.valChangeVerif, 1000); 
      },
      // 
      valChangeVerif() {
        console.log(this.delayedtimeVerif);
        if(this.delayedtimeVerif>0){
          this.emailButtontext=this.delayedtimeVerif+'s';
          this.delayedtimeVerif--;
        }else{
          clearInterval(this.timer);
          this.emailButtontext='';
          this.delayedtimeVerif=60;
          this.emailButtondisabled = false;
        }
      },
      // 
      stopTimerVerif() {
        clearInterval(this.timer);
        this.delayedtimeVerif=60;
      },
      /*********************     ***********************/
      // 
      startTimerComplete(){
        this.timer = setInterval(this.valChangeComplete, 1000); 
      },
      // 
      valChangeComplete() {
        if(this.delayedtimeComplete>0){
          this.delayedtimeComplete--;
        }else{
          clearInterval(this.timer);
          this.$router.push("/");
        }
      },
      // 
      stopTimerComplete() {
        clearInterval(this.timer);
        this.delayedtimeComplete=5;
      },
      /*********************    **************************/
      toLogin(){
        this.$router.push("/");
      }
      /**************************************************************/
    }
  }
</script>

<style scoped>
  .verifyform {
    width: 350px;
    height: 32px;
  }
  .verifytext {
    float: left;
    display: inline;
    width: 160px;
    height: 32px;
    margin-right: 5px;
  }
  .verifyimg {
    display: inline;
    width: 100px;
    height: 36px;
  }
  .updatePassword-box {
    border: 1px solid #DCDFE6;
    width: 600px;
    margin: 180px auto;
    padding: 30px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    box-shadow: 0 0 25px #909399;
  }
  .updatePassword-title {
    text-align: center;
    margin: 0 auto 40px auto;
    color: #303133;
  }
  .tabs-box {
    width: 350px;
    margin: 40px auto;
  }
</style>