<template>
  <div class="mainlayout">
    <el-form label-width="80px" >
      <div align="center" style="margin:20px;"></div>
      <el-form-item label="">
        <el-input type="text" disabled="disabled" v-model="phonenumber"/>
      </el-form-item>
      <el-form-item label="" class="editbox">
        <el-input type="text" disabled="disabled" v-model="name"/>
        <el-button class="editbutton" text="text" v-on:click="updateNamedialogVisible=true"></el-button>
      </el-form-item>
      <el-form-item label="">
        <el-input type="text" disabled="disabled" v-model="email"/>
      </el-form-item>
      <el-form-item label="">
        <el-input type="text" disabled="disabled" v-model="adminphone"/>
      </el-form-item>
      <el-form-item label="" class="editbox">
        <el-input type="text" disabled="disabled" v-model="adminkey"/>
        <el-button class="editbutton" text="text" v-on:click="refreshKeydialogVisible=true"></el-button>
      </el-form-item>
      <el-form-item label="">
        <el-button type="danger" v-on:click="toDestroy()"></el-button>
        <el-button type="danger" v-on:click="toReset()"></el-button>
      </el-form-item>
    </el-form>

    <el-dialog title="" v-model="updateNamedialogVisible" width="380px">
      <span>20</span>
      <br><br>
      <el-input maxlength='20' style="width:220px;" type="text" v-model="newname"/>
      <br><br><br>
      <span class="dialog-footer">
        <el-button type="primary" @click="newname='';updateNamedialogVisible=false;"> </el-button>
        <el-button type="primary" @click="updateName()"> </el-button>
      </span>
    </el-dialog>

    <el-dialog title="" v-model="refreshKeydialogVisible" width="380px">
      <span></span>
      <br><br><br>
      <span class="dialog-footer">
        <el-button type="primary" @click="refreshKeydialogVisible=false"> </el-button>
        <el-button type="primary" @click="refreshKey()"> </el-button>
      </span>
    </el-dialog>

    <el-dialog title="" v-model="dialogVisible" width="380px">
        <span>{{dialogtext}}</span>
        <br><br><br>
        <span class="dialog-footer">
          <el-button type="primary" @click="dialogVisible=false"> </el-button>
          <el-button @click="Yes()"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from '../../http';
  import { ElMessage } from 'element-plus';

  export default {
    name: 'Accountinfo',
    props: ['phonenumber'],
    data() {
      return {
        name: '',newname: '',
        email: '',adminphone: '',adminkey: '',
        dialogmode: true,//truefalse
        dialogtext: '',
        dialogVisible: false,
        updateNamedialogVisible: false,
        refreshKeydialogVisible: false
      }
    },
    computed:{},
    mounted: function(){
      /**************************    ****************************/
      let that = this;
      axios.get(this.$store.state.url+"/webapi/userdb/get_only_userdata",{
        params:{
          phonenumber: that.phonenumber
        }
      })
      .then(function(response){
        if(response.data.getdata==1){
          console.log(response);
          that.name = response.data.data.name;
          that.email = response.data.data.email;
          that.adminphone = response.data.data.adminphone;
          that.adminkey = response.data.data.adminkey;
        }else if(response.data.getdata==0){
          console.log('');
          that.loginfail();
        }
      },function(err){console.log('');});
    },
    methods: {
      /******************************    *******************************/
      updateName(){
        let that = this;
        axios.post(this.$store.state.url+"/webapi/userdb/updata_user_name",{phonenumber: that.phonenumber,newname: that.newname})
        .then(function(response){
          if(response.data.updata==2){
            that.name=that.newname;
            that.newname='';
            that.updateNamedialogVisible=false;
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            console.log('');
          }else if(response.data.updata==1){
            console.log('');
            ElMessage({
                showClose: true,
                message: '',
                type: 'error',
            });
          }else if(response.data.updata==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /*****************************    *******************************/
      refreshKey(){
        let that = this;
        axios.post(this.$store.state.url+"/webapi/userdb/refresh_key",{phonenumber: that.phonenumber})
        .then(function(response){
          if(response.data.refresh==1){
            that.adminkey=response.data.data.adminkey;
            that.refreshKeydialogVisible=false;
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            console.log('');
          }else if(response.data.refresh==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /******************************    *******************************/
      onDestroy(){
        let that = this;
        axios.post(this.$store.state.url+'/webapi/userdb/destroy_userdata',{phonenumber: that.phonenumber}).then(function(response){
          if(response.data.destroy==1){
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            sessionStorage.removeItem("loginstate");
            that.$router.push("/");
          }else if(response.data.destroy==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');})
      },
      /******************************    ******************************/
      onReset() {
        let that = this;
        axios.post(this.$store.state.url+'/webapi/userdb/reset_userdata',{phonenumber: that.phonenumber}).then(function(response){
          if(response.data.reset==1){
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            sessionStorage.removeItem("loginstate");
            that.$router.push("/");
          }else if(response.data.reset==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /****************************     *****************************/
      toDestroy() {
        this.dialogtext='';
        this.dialogmode=true;
        this.dialogVisible=true;
      },
      /***************************     ****************************/
      toReset() {
        this.dialogtext='';
        this.dialogmode=false;
        this.dialogVisible=true;
      },
      /************************    *************************/
      Yes(){
        if(this.dialogmode){
          this.onDestroy();
          this.dialogVisible=false;
        } else {
          this.onReset();
          this.dialogVisible=false;
        }
      },
      /******************************    ********************************/
      loginfail(){
        console.log('');
        sessionStorage.removeItem("loginstate");
        this.$router.push("/");
        alert('');
      },
      /**************************************************************************/
    },
  }
</script>

<style scoped>
.mainlayout {
  width: 360px;
  margin: 20px;
}
.editbox{
  width: 360px;
}
.el-input {
  width:220px;
}
.editbutton {
  width:50px;
}
.dialog-footer button:first-child {
  margin-right: 20px;
}
</style>
