<template>
  <div>
    <el-form ref="loginForm" label-width="80px" class="login-box" v-on:keyup.enter="onVerifyCaptchtoRegister()">
      <h3 class="login-title"></h3>
      <el-form-item label="">
        <el-input type="text" placeholder="" v-model="form.phonenumber"/>
      </el-form-item>
      <el-form-item label="">
        <el-input type="text" placeholder="" v-model="form.name"/>
      </el-form-item>
      <el-form-item label="">
        <el-input type="text" placeholder="" v-model="form.email"/>
      </el-form-item>
      <el-form-item label="" class="itemcl">
        <el-input type="password" placeholder="" v-model="form.password1"/>
        <label class="inputlabel">820</label>
      </el-form-item>
      <el-form-item label="">
        <el-input type="password" placeholder="" v-model="form.password2"/>
      </el-form-item>
      <el-form-item class="verifyform" label="">
        <el-input class="verifytext" type="text" placeholder="" v-model="form.captcha"/>
        <a class="verifyimg" @click="updateImgurl()">
          <img class="verifyimg" v-bind:src="form.imgurl" />
        </a>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" v-on:click="onVerifyCaptchtoRegister()"></el-button>
        <el-button v-on:click="toLogin()"></el-button>
      </el-form-item>
    </el-form>

    <el-dialog title="" v-model="dialogVisible" width="30%">
      <span>{{dialogtext}}</span><br>
      <span v-show="textVisible">1110<br>820<br></span><br>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from '../http';

  export default {
    name: "Register",
    data() {
      return {
        form: {
          phonenumber: '15050572717',
          name: '',
          email: '812901456@qq.com',
          password1: '12345678',
          password2: '12345678',
          captcha: '',
          imgurl: '',
          key: ''
        },
        textVisible: false,
        dialogVisible: false
      }
    },

    mounted: function(){
      // 
      this.updateImgurl();
    },
    methods: {
      /*************************    **************************/
      updateImgurl(){
        let that = this;
        axios.get(this.$store.state.url+"/captcha/api/math")
          .then(function(response){
            console.log(response);
            that.form.imgurl = response.data.img;
            that.form.key = response.data.key
          },function(err){console.log('');}
        );
      },
      /*************************    **************************/
      onVerifyCaptchtoRegister(){
        let that = this;
        axios.get(this.$store.state.url+"/webapi/verif_captcha",
        {
          params:{
            captcha: that.form.captcha,
            key: that.form.key,
          }
        }).then(function(response){
            console.log(response.data);
            if(response.data.verif==1){
              that.onRegister();
            }else if(response.data.verif==0){
              that.dialogtext = '';
              that.textVisible = false;
              that.dialogVisible = true;
              that.updateImgurl();
            }
          },function(err){console.log('');}
        );
      },
      /*************************    **************************/
      onRegister() {
        let that = this;
        axios.post(this.$store.state.url+"/webapi/verif_register"
          ,{
            phonenumber: that.form.phonenumber,
            name: that.form.name,
            email: that.form.email,
            password1: that.form.password1,
            password2: that.form.password2,
          }).then(function(response){
            if(response.data.insert==2){
              that.$router.push("/delay");
            }else if(response.data.insert==1){
              that.dialogtext = '';
              that.textVisible = false;
              that.dialogVisible = true;
            }else if(response.data.insert==0){
              if(that.form.phonenumber!=""&&that.form.name!=""&&that.form.email!=""&&that.form.password1!=""&&that.form.password2!="") {
                that.dialogtext = '';
                that.textVisible = true;
                that.dialogVisible = true;
              } else{
                that.dialogtext = '';
                that.textVisible = false;
                that.dialogVisible = true;
              }
            }
          },function(err){console.log("err");}
        );
      },
      /***********************    *************************/
      toLogin(){
        this.$router.push("/");
      }
      /***************************************************************/
    }
  }
</script>

<style lang="scss" scoped>
  .verifyform {
    width: 350px;
    height: 32px;
  }
  .verifytext {
    float: left;
    display: inline;
    width: 160px;
    height: 32px;
    margin-right: 5px;
  }
  .verifyimg {
    display: inline;
    width: 100px;
    height: 36px;
  }
  .login-box {
    border: 1px solid #DCDFE6;
    width: 350px;
    margin: 180px auto;
    padding: 35px 35px 15px 35px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    box-shadow: 0 0 25px #909399;
  }
  .login-title {
    text-align: center;
    margin: 0 auto 40px auto;
    color: #303133;
  }
  .itemcl {
    width: 350px;
    height: 32px;
    margin: 0px 0px 35px 0px;
  }
  .inputlabel {
    width: 280px;
    height: 32px;
    text-align: left;
    font-size: 10px;
    color: #CDCDCD;
  }
</style>
