<template>
  <div>
    <div class="title"></div>
      <div class="editlist">
        <el-button class="editbutton" type="primary" @click="insertUpdataOpen(true)"></el-button>
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" type="primary" @click="selectionDialogVisible=true"></el-button>
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" type="danger" @click="delAllDialogVisible=true"></el-button>
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" @click="insertAllDialogVisible=true" >
          
        </el-button>

        <el-button class="editbutton"
            style="width:50px;float:right;margin: 0px 0px 0px 10px" 
            type="primary" @click="searchCommodity">
        </el-button>
        <el-input 
          class="editbutton" 
          style="width:200px;height:35px;float:right;margin: 0px 0px 0px 0px" 
          type="search" 
          maxlength='20' 
          placeholder=""
          v-model="searchtext"
          @keyup.enter="searchCommodity"
        />
      </div>
      
      <el-table style="width: 802px" :data="commodityData"
        border='border' 
        stripe='stripe'
        @selection-change="selectionChange">
        
        <el-table-column type="selection" width="54px" align="center"/>

        <el-table-column label="" type="index" width="60px" align="center">
          <template #default="scope" v-slot="{$index}">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.$index+pageSize*(currentPage-1)+1}}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="120px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.row.id}}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="235px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.row.name}}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="175px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <span style="margin:auto;">
                1{{scope.row.bigunit}}={{scope.row.bigtosmall_specs}}{{scope.row.smallunit}}
              </span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="155px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <el-button style="margin:auto;" size="small" type="" 
              @click="insertUpdataOpen(false,scope.row)">
                
              </el-button>
              <el-button style="margin:auto;" size="small" type="" 
              @click="delDialogVisible=true;removeCommodityid=scope.row.id">
                
              </el-button>
            </div>
          </template>
        </el-table-column>

      </el-table>

      <div style="display: inline-block">
        <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[5, 10, 25, 50, 200, 500]"
          :total="totalSize"
          :small="small"
          :disabled="disabled"
          :background="background"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="getData()"
          @current-change="getData()">
        </el-pagination>
      </div>
      
      <el-dialog v-bind:title="insertUpdataDgtext" v-model="insertUpdataDgVisible" width="380px">
          <div>
            <el-form label-width="70px">
              <el-form-item label="">
                <el-input type="text" maxlength='10' :disabled="insertUpdataDgModel" v-model="addupdataComm.id"/>
                <label class="inputlabel">10</label>
              </el-form-item>
              <el-form-item label="">
                <el-input type="text" maxlength='30' v-model="addupdataComm.name"/>
                <label class="inputlabel">30</label>
              </el-form-item>
              <el-form-item label="">
                <el-dropdown>
                  <div class="guigebox">
                    <span>1&nbsp;</span>
                    <el-button class="guigebtn">{{addupdataComm.bigunit}}</el-button>
                    <span>&nbsp;=&nbsp;</span>
                  </div>
                  <template #dropdown>
                    <el-dropdown-menu>
                      <el-dropdown-item v-for="(item) in bigunits" @click="setBigunit(item)">
                        <span>{{item}}</span>
                      </el-dropdown-item>
                    </el-dropdown-menu>
                  </template>
                </el-dropdown>
                <el-input class="guigeinput" maxlength='9' type="text" v-model="addupdataComm.bigtosmall_specs"/>
                <el-dropdown>
                  <el-button class="guigebtn">{{addupdataComm.smallunit}}</el-button>
                  <template #dropdown>
                    <el-dropdown-menu>
                      <el-dropdown-item v-for="(item) in smallunits" @click="setSmallunit(item)">
                        <span>{{item}}</span>
                      </el-dropdown-item>
                    </el-dropdown-menu>
                  </template>
                </el-dropdown>
                <label class="inputlabel">9</label>
              </el-form-item>
            </el-form>
          </div>
          <br>
          <span class="dialog-footer">
            <el-button type="primary" @click="insertUpdataDgVisible = false"> </el-button>
            <el-button type="primary" @click="handleInsertUpdata()"> </el-button>
          </span>
      </el-dialog>

      <el-dialog title="" v-model="selectionDialogVisible" width="380px">
          <span></span><br>
          <br><br>
          <span class="dialog-footer">
            <el-button type="primary" @click="selectionDialogVisible=false"> </el-button>
            <el-button type="primary" @click="handleSelectionDelete()"> </el-button>
          </span>
      </el-dialog>


      <el-dialog title="" v-model="delAllDialogVisible" width="380px">
          <span></span><br>
          <br><br>
          <span class="dialog-footer">
            <el-button type="primary" @click="delAllDialogVisible=false"> </el-button>
            <el-button @click="handleAllDelete()"> </el-button>
          </span>
      </el-dialog>

      <el-dialog title="" v-model="delDialogVisible" width="380px">
        <span></span><br>
        <br><br>
        <span class="dialog-footer">
          <el-button type="primary" @click="delDialogVisible = false"> </el-button>
          <el-button type="primary" @click="handleDelete()"> </el-button>
        </span>
      </el-dialog>

    <el-dialog title="" v-model="insertAllDialogVisible" width="380px" destroy-on-close>
      <span>Excel
        <el-link type="primary" v-bind:href="comlinkurl" target="_blank">
        
        </el-link>
      </span>
      <br><br>
      <div>
        <input style="border: 1px solid;" type="file" @change="changefile"/>
      </div>
      <br><br>
      <span class="dialog-footer">
        <el-button type="primary" @click="insertAllDialogVisible=false"> </el-button>
        <el-button type="primary" @click="updataExcel"> </el-button>
      </span>
    </el-dialog>

    <el-dialog title="" v-model="insertAllErrorsDialogVisible" width="480px">
      <span>Excel</span>
      <br><br>
      <div>
        <el-table :data="errorcominfos" style="overflow-y:scroll;width:802px;height:200px;font-size:10px" 
          border='border' stripe='stripe' >
          
          <el-table-column label="" type="index" width="55px" align="center">
            <template #default="scope" v-slot="{$index}">
              <div class="tablebox">
                <span style="margin:auto;">{{scope.$index+1}}</span>
              </div>
            </template>
          </el-table-column>

          <el-table-column label="" width="100px" align="center">
            <template #default="scope">
              <div class="tablebox">
                <span style="margin:auto;">{{scope.row.errorCom.id}}</span>
              </div>
            </template>
          </el-table-column>

          <el-table-column label="" width="120px" align="center">
            <template #default="scope">
              <div class="tablebox">
                <span style="margin:auto;">{{scope.row.errorCom.name}}</span>
              </div>
            </template>
          </el-table-column>

          <el-table-column label="" width="200px" align="center">
            <template #default="scope">
              <div class="tablebox">
                <span style="margin:auto;">{{scope.row.errorText}}</span>
              </div>
            </template>
          </el-table-column>

        </el-table>
      </div>
      <br><br>
      <span class="dialog-footer">
        <el-button type="primary" @click="insertAllErrorsDialogVisible=false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script >
  import axios from '../../http';
  import { ElMessage } from 'element-plus';

  export default {
    name: 'Commoditydata',
    props: ['phonenumber'],
    components:{},
    data() {
      return {
        isVisable: true,

        commodityData:[], // 
        currentPage: 1, // 
        pageSize: 50, // 
        small: false, // 
        totalSize: 0, // 
        background: true, // 
        disabled: false, // 

        bigunits: ['','','','',''],
        smallunits: ['','','','','','','',''],
        addupdataComm: {
          id: '',name: '',
          // smallunit_amount: 0,
          smallunit: '',bigunit: '',
          bigtosmall_specs: ''
        },
        formData: {},
        searchtext: '',
        removeCommodityid: '',
        delCommodityids: [],
        errorcominfos: [],

        insertUpdataDgtext: '',insertUpdataDgModel: true,insertUpdataDgVisible: false,
        selectionDialogVisible: false,
        delAllDialogVisible: false,
        delDialogVisible: false,
        insertAllDialogVisible: false,comlinkurl: '',
        insertAllErrorsDialogVisible: false,
      }
    },
    // mounted() {
    created() {
      // 
      this.comlinkurl=this.$store.state.url+'/webapi/commoditydb/download_commodity_exampletable';
      this.getData();
    },
    methods: {
      /**********    ***********/
      getData(){
        let that = this;
        axios.get(this.$store.state.url+'/webapi/commoditydb/get_search_page_commoditydata',{
          params:{
            phonenumber: that.phonenumber,
            searchtext: that.searchtext,
            pageSize: that.pageSize,
            currentPage: that.currentPage,
          }
        }).then(function(response) {
          if(response.data.getdata==1 || response.data.getdata==2){
            console.log('');
            that.totalSize = response.data.data.total;
            that.commodityData=response.data.data.data;
          }else if(response.data.getdata==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /***************************    *****************************/
      setSmallunit(item){
        this.addupdataComm.smallunit=item;
      },
      /***************************    *****************************/
      setBigunit(item){
        this.addupdataComm.bigunit=item;
      },
      /*******************    *********************/
      insertUpdataOpen(openmodel,comm){
        if(openmodel){
          // 
          this.addupdataComm.id='12345678';
          this.addupdataComm.name='100g';
          this.addupdataComm.smallunit_amount=0;
          this.addupdataComm.smallunit=this.smallunits[0];
          this.addupdataComm.bigunit=this.bigunits[0];
          this.addupdataComm.bigtosmall_specs='100';
          this.insertUpdataDgModel=false;
          this.insertUpdataDgtext='';
          this.insertUpdataDgVisible=true
        }else{
          // 
          this.addupdataComm.id=comm.id;
          this.addupdataComm.name=comm.name;
          this.addupdataComm.smallunit_amount=comm.smallunit_amount;
          this.addupdataComm.smallunit=comm.smallunit;
          this.addupdataComm.bigunit=comm.bigunit;
          this.addupdataComm.bigtosmall_specs=comm.bigtosmall_specs;
          this.insertUpdataDgModel=true;
          this.insertUpdataDgtext='';
          this.insertUpdataDgVisible=true
        }
      },
      /**********************    ***************************/
      handleInsertUpdata(){
        if(this.insertUpdataDgModel){
          this.handleUpdata();
        }else{
          this.handleInsert();
        }
      },
      /*********************   **********************/
      handleInsert(){
        let that = this;
        axios.post(this.$store.state.url+'/api/commoditydb/insert_commoditydata',{
          phonenumber: that.phonenumber,
          id: that.addupdataComm.id, // 
          name: that.addupdataComm.name, // 
          smallunit: that.addupdataComm.smallunit, // 
          bigunit: that.addupdataComm.bigunit, // 
          bigtosmall_specs: that.addupdataComm.bigtosmall_specs, // 
        }).then(function(response) {
          let insert = response.data.insert;
          if(insert==6){
            console.log('');
            console.log(response);
            that.addupdataComm.id='';
            that.addupdataComm.name='';
            that.addupdataComm.smallunit=that.smallunits[0];
            that.addupdataComm.bigunit=that.bigunits[0];
            that.addupdataComm.bigtosmall_specs='';
            that.insertUpdataDgVisible = false;
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            that.currentPage=1;
            that.getData();
          }else if(insert==5){
            console.log('');
            ElMessage({
                showClose: true,
                message: '',
                type: 'error',
            });
          }else if(insert==4){
            let whichMemberText=response.data.whichMemberText;
            console.log(whichMemberText);
            ElMessage({
                showClose: true,
                message: whichMemberText,
                type: 'error',
            });
          }else if(insert==3){
            let whichMemberText=response.data.whichMemberText;
            console.log(whichMemberText);
            ElMessage({
                showClose: true,
                message: whichMemberText,
                type: 'error',
            });
          }else if(insert==2){
            let whichMemberText=response.data.whichMemberText;
            console.log(whichMemberText);
            ElMessage({
                showClose: true,
                message: whichMemberText,
                type: 'error',
            });
          }else if(insert==1){
            console.log('');
            ElMessage({
                showClose: true,
                message: '',
                type: 'error',
            });
          }else if(insert==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /*******************   ************************/
      handleUpdata(){
        let that = this;
        axios.post(this.$store.state.url+'/webapi/commoditydb/update_commoditydata',{
          phonenumber: that.phonenumber,
          id: that.addupdataComm.id, // 
          name: that.addupdataComm.name, // 
          smallunit: that.addupdataComm.smallunit, // 
          bigunit: that.addupdataComm.bigunit, // 
          bigtosmall_specs: that.addupdataComm.bigtosmall_specs, // 
        }).then(function(response) {
          if(response.data.update==2){
            console.log('');
            that.addupdataComm.id='';
            that.addupdataComm.name='';
            that.addupdataComm.smallunit=that.smallunits[0];
            that.addupdataComm.bigunit=that.bigunits[0];
            that.addupdataComm.bigtosmall_specs='';
            that.insertUpdataDgVisible = false;
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            that.currentPage=1;
            that.getData();
          }else if(response.data.update==1){
            console.log('');
            ElMessage({
                showClose: true,
                message: '',
                type: 'error',
            });
          }else if(response.data.update==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /**********************    ***************************/
      selectionChange(selection){
        this.delCommodityids=selection;
      },
      /***********************    ********************************/
      handleSelectionDelete(){
        if(this.delCommodityids.length==0){
          this.selectionDialogVisible=false;
          ElMessage({
                showClose: true,
                message: '',
                type: 'error'
            });
        }else{
          let that = this;
          axios.post(this.$store.state.url+'/webapi/commoditydb/del_selection_commoditydata',{
            phonenumber: that.phonenumber,
            delCommodityids: that.delCommodityids,
          }).then(function(response) {
            if(response.data.delSele==1){
              console.log('');
              that.selectionDialogVisible=false;
              ElMessage({
                  showClose: true,
                  message: '',
                  type: 'success',
              });
              that.currentPage=1;
              that.getData();
            }else if(response.data.delSele==0){
              console.log('');
              that.loginfail();
            }
          },function(err){console.log('');});
        }
      },
      /***********************    ********************************/
      handleAllDelete(){
        let that = this;
        axios.post(this.$store.state.url+'/webapi/commoditydb/del_all_commoditydata',{
          phonenumber: that.phonenumber,
        }).then(function(response) {
          if(response.data.delAll==1){
            console.log('');
            that.delAllDialogVisible=false;
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            that.currentPage=1;
            that.getData();
          }else if(response.data.delAll==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /******************  Excel  ***************************/
      changefile(event){
        let files = event.target.files[0];
        this.formData = new FormData();
        this.formData.append('phonenumber',this.phonenumber);
        this.formData.append('addCommoditysfile',files)
      },
      /**************  Excel post  *******************/
      updataExcel() {
        let that = this;
        axios.post(this.$store.state.url+'/webapi/commoditydb/insert_excel_all_commoditydata',
          that.formData,
          {headers: {'Content-Type': 'multipart/form-data'}}
        ).then(function(response) {
          if(response.data.insertExcelAll==7){
            console.log('');
            ElMessage({
              showClose: true,
              message: '',
              type: 'success',
            });
            that.currentPage=1;
            that.getData();
            that.insertAllDialogVisible=false;
          }else if(response.data.insertExcelAll==6){
            console.log('');
            ElMessage({
              showClose: true,
              message: '',
              type: 'error',
            });
            that.errorcominfos=response.data.errorcominfos;
            that.insertAllDialogVisible=false;
            that.insertAllErrorsDialogVisible=true;
          }else if(response.data.insertExcelAll==3){
            // 
            let whichMemberText=response.data.whichMemberText;
            console.log(whichMemberText);
            ElMessage({
              showClose: true,
              message: whichMemberText,
              type: 'error',
            });
            that.currentPage=1;
            that.getData();
            that.insertAllDialogVisible=false;
          }else if(response.data.insertExcelAll==2){
            console.log('');
            ElMessage({
              showClose: true,
              message: '',
              type: 'error',
            });
          }else if(response.data.insertExcelAll==1){
            console.log('');
            ElMessage({
              showClose: true,
              message: '',
              type: 'error',
            });
          }else if(response.data.insertExcelAll==0){
            console.log('');
            that.loginfail();
          }
        },function(err){
          ElMessage({
            showClose: true,
            message: '',
            type: 'error',
          });
          console.log('');
        });
      },
      /****************************    *******************************/
      handleDelete(){
        let that = this;
        axios.post(this.$store.state.url+'/webapi/commoditydb/del_commoditydata',{
          phonenumber: that.phonenumber,
          removeCommodityid: that.removeCommodityid
        }).then(function(response) {
           if(response.data.del==1){
            console.log('');
            that.removeCommodityid='';
            that.delDialogVisible=false;
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            that.currentPage=1;
            that.getData(); // 
          }else if(response.data.del==0){
            console.log('');
            ElMessage({
                showClose: true,
                message: '',
                type: 'error',
            });
          }
        },function(err){console.log('');});
      },
      /****************************    *****************************/
      searchCommodity(){
        this.getData();
      },
      /****************************    *******************************/
      loginfail(){
        console.log('');
        sessionStorage.removeItem("loginstate");
        this.$router.push("/");
        alert('');
      },
      /***********************************************************************/
    }
  }
</script>

<style scoped>
.title {
  font-size: 18px;
  line-height: 35px;
  text-align: left;
  width: 801px;
  height: 35px;
  margin: 0px 0px 15px 0px;
  border-bottom: 1px solid var(--el-color-primary-light-3);
}
.editlist {
  float: left;
  width: 802px;
  height: 35px;
  margin: 8px 0px 8px 0px;
}
.editbutton {
  float: left;
  width: 115px;
  height: 35px;
  padding: 0px;
  margin: 0px 10px 0px 0px;
}
.el-table {
  border: 1px solid;
  border-radius: 5px;
}
.tablebox {
  display: flex;
  align-items: center;
}
.el-form {
  width: 320px;
  margin: 0px auto;
}
.el-form-item {
  width: 320px;
  height: 32px;
  margin: 0px auto 40px auto;
}
.el-input {
  width: 225px;
}
.inputlabel {
  width: 260px;
  height: 32px;
  text-align: left;
  font-size: 10px;
  color: #CDCDCD;
}
.guigebox {
  width: 60px;
  height: 32px;
  text-align: center;
  line-height: 32px;
}
.guigeinput {
  width: 133px;
}
.guigebtn {
  width: 32px;
  height: 32px;
}
.dialog-footer button:first-child {
  margin-right: 20px;
}
.el-pagination {
  margin:30px auto;
}
.el-link {
  display: inline-block;
  width: 50px;
  height: 27px;
  margin: 0px 10px 0px 0px;
  padding-top: 8px;
  font-size: 5px;
  text-align: center;
}
</style>
