<template>
  <div>
    <el-form ref="loginForm" label-width="80px" class="login-box" v-on:keyup.enter="onVerifyCaptchtoLogin()">
        <h3 class="login-title">Web</h3>
        <el-form-item label="">
        <el-input type="text" placeholder="" v-model="form.phonenumber"/>
        </el-form-item>
        <el-form-item label="">
        <el-input type="password" placeholder="" v-model="form.password"/>
        </el-form-item>
        <el-form-item class="verifyform" label="">
        <el-input class="verifytext" type="text" placeholder="" v-model="form.captcha"/>
        <a class="verifyimg" @click="updateImgurl()">
          <img class="verifyimg" v-bind:src="form.imgurl" />
        </a>
        </el-form-item>
        <el-form-item>
        <el-button type="primary" v-on:click="onLogin()" ></el-button>
        <el-button v-on:click="toRegister()"></el-button>
        <el-button v-on:click="toUpdatePassword()"></el-button>
        </el-form-item>
    </el-form>

    <el-dialog title="" v-model="dialogVisible" width="30%">
        <span>{{dialogtext}}</span><br>
        <span v-show="textVisible">1110<br>820<br></span><br>
        <span class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from '../http';

  export default {
    name: "Login",
    components: {  },
    data() {
        return {
          form: {
            phonenumber: '15050572717',
            password: '12345678',
            captcha: '',imgurl: '',key: ''
          },
          dialogtext: '',
          textVisible: false,
          dialogVisible: false,
        }
    },
    computed: {},
    mounted(){
      // 
      this.updateImgurl();
      sessionStorage.removeItem("loginstate");
    },
    methods: {
      /**************************    **************************/
      updateImgurl(){
        let that = this;
        axios.get(this.$store.state.url+"/captcha/api/math")
          .then(function(response){
              that.form.imgurl = response.data.img;
              that.form.key = response.data.key
          },function(err){console.log('');}
        );
      },
      /**************************    **************************/
      onVerifyCaptchtoLogin(){
        let that = this;
        axios.get(this.$store.state.url+"/webapi/verif_captcha",
        {
          params:{
            captcha: that.form.captcha,
            key: that.form.key,
          }
        }).then(function(response){
              if(response.data.verif==1){
                that.onLogin();
              }else if(response.data.verif==0){
                that.dialogtext = '';
                that.textVisible = false;
                that.dialogVisible = true;
                that.updateImgurl();
              }
          },function(err){console.log('');}
        );
      },
      /**************************    **************************/
      onLogin() {
        // console.log(this.form.phonenumber);
        // console.log(this.form.password);
        let that = this;
        axios.get(this.$store.state.url+"/webapi/verif_login",
        {
          params:{
            phonenumber: that.form.phonenumber,
            password: that.form.password
          }
        }).then(function(response){
            if(response.data.verif==-1){
              // 
              if(that.form.phonenumber!="" && that.form.password!="") {
                that.dialogtext = '';
                that.textVisible = true;
                that.dialogVisible = true;
              } else {
                that.dialogtext = '';
                that.textVisible = false;
                that.dialogVisible = true;
              }
            }else if(response.data.verif==0){
              that.dialogtext = '';
              that.textVisible = false;
              that.dialogVisible = true;
            }else if(response.data.verif==1){
              that.dialogtext = '';
              that.textVisible = false;
              that.dialogVisible = true;
            }else if(response.data.verif==2){
              sessionStorage.setItem('loginstate','ok')
              that.$router.push("/main/"+that.form.phonenumber);
            }
          },function(err){console.log("");}
        );
      },
      /***********************    ************************/
      toRegister() {
        this.$router.push("/register");
      },
      /*********************    ***********************/
      toUpdatePassword() {
        this.$router.push("/updatepassword");
      }
      /***************************************************************/
    }
  }
</script>

<style lang="scss" scoped>
  .verifyform {
    width: 350px;
    height: 32px;
  }
  .verifytext {
    float: left;
    display: inline;
    width: 160px;
    height: 32px;
    margin-right: 5px;
  }
  .verifyimg {
    display: inline;
    width: 100px;
    height: 36px;
  }
  .login-box {
    border: 1px solid #DCDFE6;
    width: 350px;
    margin: 180px auto;
    padding: 35px 35px 15px 35px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    box-shadow: 0 0 25px #909399;
  }
  .login-title {
    text-align: center;
    margin: 0 auto 40px auto;
    color: #303133;
  }
</style>
