<template>
  <div>
      <div class="title">
        <el-button 
            class="editbutton" 
            style="margin:0px 10px 0px 0px;width:80px" 
            type="" 
            @click="toBack()">
            <el-icon><ArrowLeft/></el-icon>&nbsp;
        </el-button>
        <span style="line-height:35px;float:left;margin-left:10px"></span>
        
      </div>

      <div class="editlist">
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" 
            type="primary" @click="verifStatesToDownloadExcel()">
          
        </el-button>
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" 
            type="primary" @click="batchSelect()">
          
        </el-button>
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" 
            type="danger" @click="clearSelected()">
          
        </el-button>
        <el-label style="font-size:13px;line-height:35px;float:left;margin-left:0px">
          
        </el-label>
      </div>
      
      <el-table ref="stocktable" style="width:802px" :data="stockPersons"
        stripe='stripe' 
        @selection-change="selectionChange">
        
        <el-table-column type="selection" width="55px" align="center"/>

        <el-table-column label="" type="index" width="64px" align="center">
          <template #default="scope" v-slot="{$index}">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.$index+pageSize*(currentPage-1)+1}}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="200px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.row.phonenumber}}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="190px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.row.name}}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="190px" align="center">
          <template #default="scope">
            <div class="tablebox">
                <!--  0 -->
                <span style="margin:auto;color:red;" v-if="scope.row.state==0">
                  {{scope.row.statetext}}
                  <el-icon style="float:right;padding-left:6px;padding-top:3px">
                    <Bell/>
                  </el-icon>
                </span>
                <!--  1 -->
                <span style="margin:auto;color:orange;" v-if="scope.row.state==1">
                  {{scope.row.statetext}}
                  <el-icon style="float:right;padding-left:6px;padding-top:3px">
                    <AlarmClock/>
                  </el-icon>
                </span>
                <!--  2 -->
                <span style="margin:auto;color:blue;" v-if="scope.row.state==2">
                  {{scope.row.statetext}}
                  <el-icon style="float:right;padding-left:6px;padding-top:3px">
                    <DocumentChecked/>
                  </el-icon>
                </span>
                <!--  3 -->
                <span style="margin:auto;color:green;" v-if="scope.row.state==3">
                  {{scope.row.statetext}}
                  <el-icon style="float:right;padding-left:6px;padding-top:3px">
                    <CircleCheck/>
                  </el-icon>
                </span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="100px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.row.subtotal}}</span>
            </div>
          </template>
        </el-table-column>
      </el-table>

      <span style="font-size:15px;float:left;line-height:35px;margin:2px 0px 0px 25px">
          &nbsp;{{stockPersons.length}}&nbsp;
      </span>
  </div>
</template>

<script>
  import axios from '../../http';
  import { ElMessage } from 'element-plus';

  export default {
    name: 'Orderinfo',
    props: ['phonenumber','orderid'],
    data() {
      return {
        stockPersons: [],
        sumStockPersons: [],

        totalSize: 0,
        currentPage: 1, // 
        pageSize: 5, // 
        small: false, // 
        totalSize: 0, // 
        background: true, // 
        disabled: false, // 
      }
    },
    computed:{},
    mounted: function(){
      /************************    ***************************/
      this.getData();
    },
    methods: {
      /*************************    ****************************/
      toBack(){
        this.$router.push("/user/inventoryorder/"+this.phonenumber);
      },
      /********************    **********************/
      getData(){
        let that = this;
        axios.get(this.$store.state.url+'/webapi/orderdb/get_only_order_info',{
          params:{
            sendphonenumber: that.phonenumber,
            orderid: that.orderid
          }
        }).then(function(response) {
          if(response.data.get==1){
            that.stockPersons=response.data.data;
            that.totalSize=response.data.data.length;
            console.log('');
          }else if(response.data.get==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /****************************    *******************************/
      verifStatesToDownloadExcel(){
        // console.log(this.sumStockPersons);
        if(this.sumStockPersons.length>0){
          let that = this;
          axios.post(this.$store.state.url+'/webapi/orderdb/verif_states',{
            sendphonenumber: that.phonenumber,
            orderid: that.orderid,
            sumPhonenumbers: that.sumStockPersons
          }).then(function(response) {
            if(response.data.verif==2){
              that.downloadExcel();
              console.log('');
            }else if(response.data.verif==1){
              ElMessage({
                showClose: true,
                message: '',
                type: 'error',
              });
              console.log('');
            }else if(response.data.verif==0){
              console.log('');
              that.loginfail();
            }
          },function(err){console.log('');});
        }if(this.sumStockPersons.length==0){
          ElMessage({
            showClose: true,
            message: '',
            type: 'error',
          });
        }
      },
      /****************************    ****************************/
      batchSelect(){
        this.clearSelected();
        let that = this;
        this.stockPersons.forEach(function(element){
          if(element.state==2 || element.state==3){
            that.sumStockPersons.push(element);
            that.$refs.stocktable.toggleRowSelection(element);
          }
        });
      },
      /****************************    ****************************/
      clearSelected() {
        this.$refs.stocktable.clearSelection();
      },
      /************************  Excel  *************************/
      downloadExcel(){
        let that = this;
        axios.post(this.$store.state.url+'/webapi/orderdb/download_stock',{
          sendphonenumber: that.phonenumber,
          orderid: that.orderid,
          sumPhonenumbers: that.sumStockPersons
        },{responseType: 'blob'}).then(function(response) {
          let filename = that.getFormatDateTimeFileName();
          let downloadElement = document.createElement('a'); //a
          let blob = new Blob([response.data]) //
          let href = window.URL.createObjectURL(blob); //
          downloadElement.href = href;
          downloadElement.download = filename; //
          document.body.appendChild(downloadElement); //body
          downloadElement.click(); //a
          document.body.removeChild(downloadElement); //
          window.URL.revokeObjectURL(href); //blob
          ElMessage({
            showClose: true,
            message: '',
            type: 'success',
          });
          that.getData();
          console.log('');
        },function(err){
          ElMessage({
            showClose: true,
            message: '',
            type: 'error',
          });
          console.log('');
        });
      },
      /**************************    *****************************/
      getFormatDateTimeFileName(){
        let date = new Date();
        let year= date.getFullYear();
        let month = date.getMonth() + 1;
        let day = date.getDate();
        let hour = date.getHours();
        let minute = date.getMinutes();
        let second = date.getSeconds();
        const min = 10000;
        const max = 99999;
        let lastname = Math.floor(Math.random()*(max-min))+min;
        return year+""+month+""+day+""+hour+""+minute+""+second+""+lastname+".xlsx";
      },
      /*********************    ***************************/
      selectionChange(selection){
        // console.log(selection)
        this.sumStockPersons=selection;
      },
      /***************************    **********************************/
      loginfail(){
        console.log('');
        sessionStorage.removeItem("loginstate");
        this.$router.push("/");
        alert('');
      },
      /*************************************************************************/
    },
  }
</script>

<style scoped>
.title {
  font-size: 18px;
  line-height: 35px;
  text-align: center;
  width: 801px;
  height: 35px;
  margin: 0px 0px 15px 0px;
}
.editlist {
  float: left;
  width: 802px;
  height: 35px;
  margin: 8px 0px 8px 0px;
}
.editbutton {
  float: left;
  width: 115px;
  height: 35px;
  padding: 0px;
  margin: 0px 10px 0px 0px;
}
.el-table {
  border: 1px solid;
  border-radius: 5px;
}
.tablebox {
  display: flex;
  align-items: center;
}
.el-form {
  width: 320px;
  margin: 0px auto;
}
.el-form-item {
  width: 320px;
  height: 32px;
  margin: 0px auto 40px auto;
}
.el-input {
  width: 225px;
}
.inputlabel {
  width: 260px;
  height: 32px;
  text-align: left;
  font-size: 10px;
  color: #CDCDCD;
}
.guigebox {
  width: 60px;
  height: 32px;
  text-align: center;
  line-height: 32px;
}
.guigeinput {
  width: 133px;
}
.guigebtn {
  width: 32px;
  height: 32px;
}
.dialog-footer button:first-child {
  margin-right: 5px;
}
.el-pagination {
  margin:30px auto;
}
</style>
