<template>
  <div>
    <div class="title"></div>
      <div class="editlist">
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" type="primary" @click="insertDialogVisible=true"></el-button>
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" type="primary" @click="selectionDialogVisible=true"></el-button>
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" type="danger" @click="delAllDialogVisible=true"></el-button>
        <el-button class="editbutton" style="margin:0px 10px 0px 0px" @click="insertAllDialogVisible=true" >
          
        </el-button>

        <el-button class="editbutton"
            style="width:50px;float:right;margin: 0px 0px 0px 10px" 
            type="primary" @click="searchUsers">
        </el-button>
        <el-input 
          class="editbutton" 
          style="width:200px;height:35px;float:right;margin: 0px 0px 0px 0px" 
          type="search" 
          maxlength='30' 
          placeholder=""
          v-model="searchtext"
          @keyup.enter="searchUsers"
        />
      </div>
      
      <el-table :data="adminData" v-loading="loading" style="width: 802px"
              border='border' stripe='stripe' @selection-change="selectionChange">

        <el-table-column type="selection" width="54px" align="center"/>
        
        <el-table-column label="" type="index" width="80px" align="center">
          <template #default="scope" v-slot="{$index}">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.$index+pageSize*(currentPage-1)+1}}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="240px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.row.phonenumber}}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="200px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <span style="margin:auto;">{{scope.row.name}}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column label="" width="225px" align="center">
          <template #default="scope">
            <div class="tablebox">
              <el-button style="margin:auto;" size="small" type="" 
              @click="this.removePhonenumber=scope.row.phonenumber;delDialogVisible=true;">
                
              </el-button>
            </div>
          </template>
        </el-table-column>

      </el-table>

      <div style="display: inline-block">
        <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[5, 10, 25, 50]"
          :total="totalSize"
          :small="small"
          :disabled="disabled"
          :background="background"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="getData()"
          @current-change="getData()">
        </el-pagination>
      </div>

      <el-dialog title="" v-model="insertDialogVisible" width="380px">
          <div>
            <el-form label-width="75px">
              <el-form-item label="">
                <el-input type="text" maxlength='11' v-model="addPhonenumber"/>
              </el-form-item>
              <el-form-item label="">
                <el-input type="text" maxlength='5' v-model="adminkey"/>
              </el-form-item>
            </el-form>
          </div>
          <br>
          <span class="dialog-footer">
            <el-button type="primary" @click="insertDialogVisible = false"> </el-button>
            <el-button type="primary" @click="handleInsert()"> </el-button>
          </span>
      </el-dialog>

      <el-dialog title="" v-model="selectionDialogVisible" width="380px">
          <span></span><br>
          <br><br>
          <span class="dialog-footer">
            <el-button type="primary" @click="selectionDialogVisible=false"> </el-button>
            <el-button type="primary" @click="handleSelectionDelete()"> </el-button>
          </span>
      </el-dialog>

      <el-dialog title="" v-model="delAllDialogVisible" width="380px">
          <span></span><br>
          <br><br>
          <span class="dialog-footer">
            <el-button type="primary" @click="delAllDialogVisible = false"> </el-button>
            <el-button @click="handleAllDelete()"> </el-button>
          </span>
      </el-dialog>

      <el-dialog title="" v-model="insertAllDialogVisible" width="380px">
          <span>Excel
          <el-link type="primary" v-bind:href="userlinkurl" target="_blank">
          
          </el-link>
        </span>
        <br><br>
        <div>
          <input style="border: 1px solid;" type="file" @change="changefile"/>
        </div>
        <br><br>
        <span class="dialog-footer">
          <el-button type="primary" @click="insertAllDialogVisible=false"> </el-button>
          <el-button type="primary" @click="uploadExcel"> </el-button>
        </span>
      </el-dialog>

      <el-dialog title="" v-model="insertAllErrorsDialogVisible" width="480px">
        <span>Excel</span>
        <br><br>
        <div>
          <el-table :data="erroruserinfos" style="overflow-y:scroll;height:200px;font-size:10px" 
            border='border' stripe='stripe'>
            
            <el-table-column label="" type="index" width="55px" align="center">
              <template #default="scope" v-slot="{$index}">
                <div class="tablebox">
                  <span style="margin:auto;">{{scope.$index+1}}</span>
                </div>
              </template>
            </el-table-column>

            <el-table-column label="" width="100px" align="center">
              <template #default="scope">
                <div class="tablebox">
                  <span style="margin:auto;">{{scope.row.errorUser.phonenumber}}</span>
                </div>
              </template>
            </el-table-column>

            <el-table-column label="" width="200px" align="center">
              <template #default="scope">
                <div class="tablebox">
                  <span style="margin:auto;">{{scope.row.errorText}}</span>
                </div>
              </template>
            </el-table-column>

            <el-table-column label="" width="80px" align="center">
              <template #default="scope">
                <div class="tablebox">
                  <span style="margin:auto;">{{scope.row.errorUser.adminkey}}</span>
                </div>
              </template>
            </el-table-column>

          </el-table>
        </div>
        <br><br>
        <span class="dialog-footer">
          <el-button type="primary" @click="insertAllErrorsDialogVisible=false"> </el-button>
        </span>
      </el-dialog>

      <el-dialog title="" v-model="delDialogVisible" width="380px">
        <span></span><br>
        <br><br>
        <span class="dialog-footer">
          <el-button type="primary" @click="delDialogVisible = false"> </el-button>
          <el-button type="primary" @click="handleDelete()"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from '../../http';
  import { ElMessage } from 'element-plus';

  export default {
    name: 'Submanagement',
    props: ['phonenumber'],
    data() {
      return {
        loading: false,
        adminData:[], // 
        currentPage: 1, // 
        pageSize: 10, // 
        small: false, // 
        totalSize: 0, // 
        background: true, // 
        disabled: false, // 

        addPhonenumber: '',adminkey: '',
        removePhonenumbers: [],
        removePhonenumber: '',
        searchtext: '',
        erroruserinfos: [], //

        insertDialogVisible: false,
        selectionDialogVisible: false,
        delAllDialogVisible: false,
        delDialogVisible: false,
        insertAllDialogVisible: false,userlinkurl: '',
        insertAllErrorsDialogVisible: false
      }
    },
    // mounted() {
    created() {
      // 
      this.userlinkurl=this.$store.state.url+'/webapi/userdb/download_user_exampletable';
      this.getData();
    },
    methods: {
      /*************    ****************/
      // ()
      getData(){
        let that = this;
        axios.get(this.$store.state.url+'/webapi/userdb/get_search_page_admin_phonedata',{
          params:{
            phonenumber_AdminPhone: that.phonenumber,
            searchtext: that.searchtext,
            pageSize: that.pageSize,
            currentPage: that.currentPage
          }
        }).then(function(response) {
          if(response.data.getdata==1 || response.data.getdata==2){
            that.totalSize = response.data.data.total;
            that.adminData=response.data.data.data;
            console.log('');
          }else if(response.data.getdata==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /*************************    ******************************/
      handleInsert(){
        if(this.addPhonenumber=="" || this.adminkey==""){
          ElMessage({
              showClose: true,
              message: '',
              type: 'error',
          });
        }else{
          let that = this;
          axios.post(this.$store.state.url+'/webapi/userdb/insert_admin_phone',{
            phonenumber: that.phonenumber,
            addPhonenumber: that.addPhonenumber,
            adminkey: that.adminkey,
          }).then(function(response) {
            if(response.data.insert==5){
              that.addPhonenumber='';
              that.adminkey='';
              that.insertDialogVisible = false;
              ElMessage({
                  showClose: true,
                  message: '',
                  type: 'success',
              });
              that.currentPage=1;
              that.getData();
              console.log('');
            }else if(response.data.insert==4){
              console.log('');
              ElMessage({
                  showClose: true,
                  message: '',
                  type: 'error',
              });
            }else if(response.data.insert==3){
              console.log('');
              ElMessage({
                  showClose: true,
                  message: '',
                  type: 'error',
              });
            }else if(response.data.insert==2){
              console.log('');
              ElMessage({
                  showClose: true,
                  message: '',
                  type: 'error',
              });
            }else if(response.data.insert==1){
              console.log('');
              ElMessage({
                  showClose: true,
                  message: '',
                  type: 'error',
              });
            }else if(response.data.insert==0){
              console.log('');
              that.loginfail();
            }
          },function(err){console.log('');});
        }
      },
      /***********************    ***************************/
      selectionChange(selection){
        this.removePhonenumbers=selection;
      },
      /*************************    ******************************/
      handleSelectionDelete(){
          if(this.removePhonenumbers.length==0){
          this.selectionDialogVisible=false;
          ElMessage({
                showClose: true,
                message: '',
                type: 'error'
            });
        }else{
          let that = this;
          axios.post(this.$store.state.url+'/webapi/userdb/del_selection_admin_phone',{
            phonenumber: that.phonenumber,
            removePhonenumbers: that.removePhonenumbers,
          }).then(function(response) {
            if(response.data.delSele==1){
              console.log('');
              that.selectionDialogVisible=false;
              ElMessage({
                  showClose: true,
                  message: '',
                  type: 'success',
              });
              that.currentPage=1;
              that.getData();
            }else if(response.data.delSele==0){
              console.log('');
              that.loginfail();
            }
          },function(err){console.log('');});
        }
      },
      /*************************    ******************************/
      handleAllDelete(){
        let that = this;
        axios.post(this.$store.state.url+'/webapi/userdb/del_all_admin_phone',{
          phonenumber: that.phonenumber,
        }).then(function(response) {
          if(response.data.delAll==1){
            that.delAllDialogVisible = false;
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            that.currentPage=1;
            that.getData();
            console.log('');
          }else if(response.data.delAll==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /******************  Excel  ***************************/
      changefile(event){
        let files = event.target.files[0];
        this.formData = new FormData();
        this.formData.append('phonenumber',this.phonenumber);
        this.formData.append('addUsersfile',files);
      },
      /*************  Excel post  ********************/
      uploadExcel() {
        let that = this;
        axios.post(this.$store.state.url+'/webapi/userdb/insert_excel_all_userdata',
          that.formData,
          {headers: {'Content-Type': 'multipart/form-data'}}
        ).then(function(response) {
          console.log(response.data);
          let insertExcelAll = response.data.insertExcelAll;
          if(insertExcelAll==9){
            console.log('');
            ElMessage({
              showClose: true,
              message: '',
              type: 'success',
            });
            that.currentPage=1;
            that.getData();
            that.insertAllDialogVisible=false;
          }else if(insertExcelAll==8){
            console.log('');
            ElMessage({
              showClose: true,
              message: '',
              type: 'error',
            });
            that.erroruserinfos=response.data.erroruserinfos;
            that.insertAllDialogVisible=false;
            that.currentPage=1;
            that.getData();
            that.insertAllErrorsDialogVisible=true;
          }else if(insertExcelAll==3){
            let whichMemberText = response.data.whichMemberText;
            console.log(whichMemberText);
            ElMessage({
              showClose: true,
              message: whichMemberText,
              type: 'error',
            });
            that.insertAllDialogVisible=false;
          }else if(insertExcelAll==2){
            console.log('');
            ElMessage({
              showClose: true,
              message: '',
              type: 'error',
            });
            that.insertAllDialogVisible=false;
          }else if(insertExcelAll==1){
            console.log('');
            ElMessage({
              showClose: true,
              message: '',
              type: 'error',
            });
            that.insertAllDialogVisible=false;
          }else if(insertExcelAll==0){
            console.log('');
            that.loginfail();
          }
        },function(err){
          ElMessage({
            showClose: true,
            message: '',
            type: 'error',
          });
          console.log('');
        });
      },
      /*******************    **************************/
      handleDelete(){
        let that = this;
        axios.post(this.$store.state.url+'/webapi/userdb/del_admin_phone',{
          phonenumber: that.phonenumber,
          removePhonenumber: that.removePhonenumber
        }).then(function(response) {
          if(response.data.del==1){
            that.delDialogVisible = false
            ElMessage({
                showClose: true,
                message: '',
                type: 'success',
            });
            that.currentPage=1;
            that.getData();
            console.log('');
          }else if(response.data.del==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /*****************************    *******************************/
      searchUsers(){
        this.getData();
      },

      /*****************************    *******************************/
      loginfail(){
        console.log('');
        sessionStorage.removeItem("loginstate");
        this.$router.push("/");
        alert('');
      },
      /***********************************************************************/
    }
  }
</script>

<style scoped>
.title {
  font-size: 18px;
  line-height: 35px;
  text-align: left;
  width: 801px;
  height: 35px;
  margin: 0px 0px 15px 0px;
  border-bottom: 1px solid var(--el-color-primary-light-3);
}
.editlist {
  float: left;
  width: 801px;
  height: 35px;
  margin: 8px 0px 8px 0px;
}
.editbutton {
  float: left;
  width: 115px;
  height: 35px;
}
.el-table {
  border: 1px solid;
  border-radius: 5px;
}
.tablebox {
  display: flex;
  align-items: center;
}
.el-form {
  width:300px;
  margin:0px auto;
}
.el-input {
  width:180px;
}
.dialog-footer button:first-child {
  margin-right: 20px;
}
.el-pagination {
  margin:30px auto;
}
.el-link {
  display: inline-block;
  width: 50px;
  height: 27px;
  margin: 0px 10px 0px 0px;
  padding-top: 8px;
  font-size: 5px;
  text-align: center;
}
</style>
