<template>
  <div>
    <div class="title"></div>
    <div class="editlist">
      <el-button class="editbutton" type="primary" @click="openCreateOrderDialog()"></el-button>
    </div>

    <div class="cardlist">
      <el-card class="boxcard" v-for="carditem in orderList">
        <template #header>
          <div class="card-header">
            <span>{{carditem.orderid}}</span>

            <el-dropdown>
              <span>
                <el-icon style="padding-top:3px">
                  <arrow-down />
                </el-icon>
              </span>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item @click="openDelDialog(carditem.orderid,true)">
                    
                  </el-dropdown-item>
                  <!-- <el-dropdown-item></el-dropdown-item> -->
                </el-dropdown-menu>
              </template>
            </el-dropdown>

            <el-button class="cardbutton" type="primary" @click="toOrderinfo(carditem.orderid)"></el-button>
          </div>
        </template>
        
        <div class="carditem">
          <span style="float:left">{{carditem.subtime}}</span>
        </div>

        <div class="carditem">
          <span style="float:left">{{carditem.statetext}}&nbsp;&nbsp;</span>
          <el-icon style="float:left;padding-top:8px">
            <!--  0 -->
            <Bell v-if="carditem.state==0"/>
            <!--  1 -->
            <AlarmClock  v-if="carditem.state==1"/>
            <!--  2 -->
            <Cpu  v-if="carditem.state==2"/>
            <!--  3 -->
            <Loading  v-if="carditem.state==3"/>
            <!--  4 -->
            <Finished v-if="carditem.state==4"/>
          </el-icon>
          <el-button class="delbtn" type="danger" @click="openDelDialog(carditem.orderid,false)"><el-icon><Delete/></el-icon></el-button>
        </div>
      </el-card>
    </div>

    <el-dialog title="" v-model="newOrderdialogVisible" width="800px" destroy-on-close>
      <span></span><br>
      <span></span>
      <br><br>
        <el-transfer
          style="text-align: left; display: inline-block"
          :data="users"
          v-model="rightValue"
          filterable
          filter-placeholder=""
          target-order="unshift"
          :titles="['', '']"
          :button-texts="['', '']"
          :format="{
            noChecked: '${total}',
            hasChecked: '${checked}/${total}',
          }"
          @change="transferChange"
        >
        </el-transfer>
      <br><br>
      <span class="dialog-footer">
        <el-button type="primary" @click="checkAll=true;newOrderdialogVisible=false"> </el-button>
        <el-button type="primary" @click="createOrder()"> </el-button>
      </span>
    </el-dialog>

    <el-dialog title="" v-model="delDialogVisible" width="380px">
        <span v-if="!Manddelmode"></span>
        <span v-if="Manddelmode" style="color:red"></span>
        <br><br><br>
        <span class="dialog-footer">
          <el-button type="primary" @click="delDialogVisible=false"> </el-button>
          <el-button v-if="!Manddelmode" @click="deleteOrder()"> </el-button>
          <el-button v-if="Manddelmode" @click="delOrderMandatoryCode()"> </el-button>
        </span>
    </el-dialog>
    
  </div>
</template>

<script>
  import axios from '../../http';
  import { ElMessage } from 'element-plus';

  export default {
    name: 'Inventoryorder',
    props: ['phonenumber'],
    data() {
      return {
        // 
        orderList: [],
        // 
        users: [],
        rightValue: [],
        // 
        delorderid: '',
        // 
        newOrderdialogVisible: false,
        Manddelmode: false, // falsetrue
        delDialogVisible: false,
      }
    },
    // mounted() {
    created() {
      this.getData();
    },
    methods: {
      /***************************    *******************************/
      getData(){
        let that = this;
        axios.get(this.$store.state.url+'/webapi/orderdb/get_order_list',{
          params:{
            sendphonenumber: that.phonenumber
          }
        }).then(function(response) {
          if(response.data.get==1){
            console.log(response);
            that.orderList=response.data.data;
            console.log('');
          }else if(response.data.get==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /**************************    *******************************/
      checkAllUsers() {
        this.checkedUsers = this.checkAll ? this.users : [];
        this.isIndeterminate = false;
      },
      /****************************    **********************************/
      checkedUsersChange() {
        let checkedCount = this.checkedUsers.length;
        this.checkAll = checkedCount === this.users.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.users.length;
      },
      /************************    ***************************/
      openCreateOrderDialog() {
        let that = this;
        axios.get(this.$store.state.url+'/webapi/userdb/get_only_admin_phonedata_totransfer',{
          params:{
            phonenumber: that.phonenumber
          }
        }).then(function(response) {
          if(response.data.get==1){
            console.log(response.data.data);
            that.rightValue=[];
            that.users=response.data.data;
            that.users.forEach(item => {
              that.rightValue.push(item.key);
            });
            that.newOrderdialogVisible=true;
            console.log('');
          }else if(response.data.get==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /**************************    ******************************/
      transferChange(){
        // console.log('transferChange:'+this.rightValue);
      },
      /****************************    **********************************/
      createOrder(){
        this.newOrderdialogVisible=false;
        let that = this;
        axios.post(this.$store.state.url+'/webapi/orderdb/create_order',{
          sendphonenumber: that.phonenumber,
          recphonenumbers: that.rightValue,
        }).then(function(response) {
          let create = response.data.create;
          if(create==5){
            that.rightValue=[];
            ElMessage({
              showClose: true,
              message: '',
              type: 'success',
            });
            that.getData();
            console.log('');
          }else if(create==2 || create==3 || create==4){
            // 
            let whichMemberText = response.data.whichMemberText;
            console.log(whichMemberText);
            ElMessage({
              showClose: true,
              message: whichMemberText,
              type: 'error',
            });
          }else if(create==1){
            console.log('');
            ElMessage({
              showClose: true,
              message: '',
              type: 'error',
            });
          }else if(create==0){
            console.log('');
            that.loginfail();
          }
        },function(err){that.getData();console.log('');});
      },
      /*************************    *******************************/
      toOrderinfo(orderid){
        this.$router.push("/user/orderinfo/"+this.phonenumber+'/'+orderid);
      },
      /***********************    ******************************/
      openDelDialog(orderid,Manddelmode){
        this.delorderid=orderid;
        this.Manddelmode=Manddelmode;
        this.delDialogVisible=true;
      },
      /***************************    ***********************************/
      deleteOrder(){
        let that = this;
        axios.post(this.$store.state.url+'/webapi/orderdb/del_order',{
          phonenumber: that.phonenumber,
          orderid: that.delorderid
        }).then(function(response) {
          if(response.data.del==2){
            that.delorderid='';
            that.delDialogVisible=false;
            ElMessage({
              showClose: true,
              message: '',
              type: 'success',
            });
            that.getData();
            console.log('');
          }else if(response.data.del==1){
            that.delorderid='';
            that.delDialogVisible=false;
            ElMessage({
              showClose: true,
              message: '',
              type: 'error',
            });
            console.log('');
          }else if(response.data.del==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /*************************    ***********************************/
      delOrderMandatoryCode(){
        let that = this;
        axios.post(this.$store.state.url+'/webapi/orderdb/del_order_mandatory_code',{
          phonenumber: that.phonenumber,
          orderid: that.delorderid
        }).then(function(response) {
          if(response.data.del==1){
            that.delorderid='';
            that.delDialogVisible=false;
            ElMessage({
              showClose: true,
              message: '',
              type: 'success',
            });
            that.getData();
            console.log('');
          }else if(response.data.del==0){
            console.log('');
            that.loginfail();
          }
        },function(err){console.log('');});
      },
      /***************************    ***********************************/
      loginfail(){
        console.log('');
        sessionStorage.removeItem("loginstate");
        this.$router.push("/");
        alert('');
      },
      /**************************************************************************/
    }
  }
</script>

<style scoped>
.title {
  font-size: 18px;
  line-height: 35px;
  text-align: left;
  width: 801px;
  height: 35px;
  margin: 0px 0px 15px 0px;
  border-bottom: 1px solid var(--el-color-primary-light-3);
}
.editlist {
  float: left;
  width: 802px;
  height: 35px;
  margin: 8px 0px 8px 0px;
}
.editbutton {
  float: left;
  width: 115px;
  height: 35px;
  padding: 0px;
  margin: 0px 10px 0px 0px;
}

.cardlist {
  display: block;
  width: 834px;
  align-items: center;
}
.boxcard {
  float: left;
  width: 400px;
  height: 203px;
  margin: 0px 15px 15px 0px;
}
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cardbutton {
  font-size: 14px;
}
.carditem {
  width: 360px;
  height: 32px;
  margin: 2px 0px 18px 0px;
  line-height:32px;
}
.delbtn {
  float:right;
}
.dialog-footer button:first-child {
  margin-right: 20px;
}
</style>
